body {
  margin: 0;
}

.people {
  --columns-count: 4;
  grid-template-columns: repeat(var(--columns-count), 220px);
  justify-content: center;
  gap: 40px 30px;
  padding-top: 30px;
  font-family: Arial, sans-serif;
  display: grid;
}

@media (width <= 999px) {
  .people {
    --columns-count: 2;
  }
}

.person {
  border-radius: 10px 10px 0 0;
  padding: 10px 10px 9px;
  transition: transform 1s linear;
  position: relative;
}

.person__photo {
  z-index: -1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  transition: border-radius 1s linear;
  display: flex;
  position: relative;
  overflow: hidden;
}

.person__photo img {
  object-fit: cover;
  width: 100%;
  height: auto;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: bold;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__details {
  text-align: center;
  margin-top: 8px;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: bold;
}

.person__extra {
  z-index: 2;
  text-wrap: balance;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  font-size: 18px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  transform: translateY(10px);
}

.person__extra span:not(:last-child):after {
  content: ", ";
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .person__photo {
  border-radius: 60px;
}

.person:hover .person__extra {
  opacity: 1;
  visibility: visible;
  background-color: #eee;
  border-radius: 0 0 10px 10px;
  padding-bottom: 16px;
  transform: translateY(0);
}
